.content.aboutme .card p {
  text-align: left;
  color: #4191B5;
  font-size: 1.3rem;
}
.content.aboutme .card .selector {
  position: relative;
  width: 110px;
  height: 13px;
  margin: 0 auto;
}
.content.aboutme .card .selector .line {
  position: absolute;
  top: 5px;
  border-bottom: 2px solid #4191B5;
  width: 100%;
}
.content.aboutme .card .selector .circle {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #4191B5;
  top: 50%;
}
.content.aboutme .card .selector .circle.active {
  width: 13px;
  height: 13px;
}
.content.aboutme .card .selector div:nth-child(2) {
  left: 0;
  transform: translateY(-50%);
}
.content.aboutme .card .selector div:nth-child(3) {
  left: 50%;
  transform: translate(-50%, -50%);
}
.content.aboutme .card .selector div:nth-child(4) {
  left: 100%;
  transform: translate(-100%, -50%);
}
.content.aboutme p {
  color: #24468F;
  margin-bottom: 30px;
  font-size: 2rem;
}
.content.aboutme .triangle {
  position: relative;
  margin-bottom: 65px;
}
.content.aboutme .triangle button {
  border: 3px solid #24468F;
  border-radius: 20px;
  padding: 5px 10px;
  background: none;
  color: black;
}
.content.aboutme .triangle button:nth-of-type(1) {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
.content.aboutme .triangle button:nth-of-type(2) {
  position: absolute;
  left: 15%;
  transform: translate(-50%);
}
.content.aboutme .triangle button:nth-of-type(3) {
  position: absolute;
  left: 85%;
  transform: translate(-50%);
}
.content.aboutme .triangle .inactive-buttons {
  border: 1px solid #707070;
}
.content.aboutme .triangle .inactive-buttons:hover {
  cursor: pointer;
}
.content.aboutme .triangle img {
  display: block;
  width: 220px;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 10px;
  transform-origin: center 67%;
}
@media screen and (min-width: 1024px) {
  .content.aboutme .top {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 80%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1024px) {
  .content.aboutme .head {
    padding-right: 5vw;
    order: 1;
  }
  .content.aboutme .head h2 {
    display: none;
  }
  .content.aboutme .head p {
    text-transform: uppercase;
    font-size: 3rem;
    font-weight: 500;
  }
}
@media screen and (min-width: 1280px) {
  .content.aboutme .head {
    padding-right: 15vw;
  }
}
@media screen and (min-width: 1024px) {
  .content.aboutme .triangle {
    width: 500px;
  }
}
@media screen and (min-width: 1024px) {
  .content.aboutme .card {
    width: 80%;
    margin: 0 auto;
  }
}

